export class AddCar {
  constructor(root) {
    this.$root = root;

    this.init();
  }

  init() {
    this.addCar = document.createElement('div');
    this.addCar.className = 'add-car';
  }

  create() {
    this.addCar.innerHTML = this.createHtml();
    this.$root.append(this.addCar);
  }

  createHtml() {
    return `
      <div class="container">
        <div class="h1">Â¡Ay caramba!</div>
      </div>
    `;
  }
}